/*---
 section: Containers
 title: Modal
 ---

 ```example:html
 <a href="#test-modal" class="c-button">Open Modal</a>

 <div class="c-modal" id="test-modal">
   <div class="c-modal__card">
     <div class="c-modal__title">Modal Title <a href="#close"></a></div>
     <p>This is a card in a modal, what will they think of next?</p>
     <div class="c-modal__actions">
       <a href="#close" class="c-button--alert">Close</a>
     </div>
   </div>
   <a href="#close" class="c-modal__close"></a>
 </div>
 ```
 */

:root {
  --modal-margin: var(--global-outter-margin);
}

@define-mixin modal $titlebar: var(--blue-800), $background: var(--grey-100) {
  @extend %start;
  @extend %row;
  @extend %fade-out;
  z-index: 500;

  &--top {
    left: var(--modal-margin);
    position: fixed;
    right: var(--modal-margin);
    top: var(--modal-margin);
  }

  &--top-right {
    position: fixed;
    right: var(--modal-margin);
    top: var(--modal-margin);
  }

  &--top-left {
    position: fixed;
    left: var(--modal-margin);
    top: var(--modal-margin);
  }

  &--bottom {
    position: fixed;
    bottom: var(--modal-margin);
    left: var(--modal-margin);
    right: var(--modal-margin);
  }

  &--bottom-right {
    position: fixed;
    bottom: var(--modal-margin);
    right: var(--modal-margin);
  }

  &--bottom-left {
    position: fixed;
    bottom: var(--modal-margin);
    left: var(--modal-margin);
  }

  &--middle {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  &:target,
  &--active {
    @extend %fade-in;
  }

  &__close {
    @extend %fade-out;
    background: rgba(0, 0, 0, .6);
    bottom: 0;
    content: '';
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 400;
  }

  &__card {
    @mixin card $background;
    padding-bottom: 1rem;
    min-height: 120px;
    min-width: 120px;
  }

  &__title {
    @extend %row-break;
    background-color: $titlebar;
    color: contrast($titlebar);
    font-size: 1.3rem;
    margin-bottom: .5rem;
    margin-top: -.5em;
    min-height: 44px;
    padding: .5rem 1rem;
    position: relative;

    .o-close i {
      color: rgba(255, 255, 255, .5);
      cursor: pointer;
      font-size: 40px;
      position: absolute;
      right: .5rem;
      top: .5rem;
    }
  }

  &__actions {
    @extend %end;
  }
}

.c-modal {
  @mixin modal;

  &--help {
    @mixin modal var(--blue-700), var(--white);

    &__title {
      @extend %row;
      @extend %middle;
      border-radius: 4px 4px 0 0;

      i:first-child {
        margin-right: 5px;
      }

      .o-close i {
        font-size: 30px !important;
        top: .4rem;
      }

      h6 {
        display: inline-block;
        margin: 5px 0 !important;
      }
    }

    &__card {
      border-radius: 4px;
    }
  }
}

