/*---
 section: Containers
 title: Tooltips
 ---

 ```example:html
 <button class="c-button c-tooltip c-tooltip--top" data-tooltip="Tooltip content goes in a data-tooltip attribute.">Top tooltip</a>
 <button class="c-button c-tooltip c-tooltip--right" data-tooltip="Tooltip content goes in a data-tooltip attribute.">Right tooltip</a>
 <button class="c-button c-tooltip c-tooltip--bottom" data-tooltip="Tooltip content goes in a data-tooltip attribute.">Bottom tooltip</a>
 <button class="c-button c-tooltip c-tooltip--left" data-tooltip="Tooltip content goes in a data-tooltip attribute.">Left tooltip</a>
 ```

 ### Customize

 ``` css
  --tooltip-arrow-size: 10px;
  --tooltip-background: var(--white);
  --tooltip-space: .5rem;
  --tooltip-z-index: 50;
  ```

 */

:root {
  --tooltip-arrow-size: 10px;
  --tooltip-background: var(--white);
  --tooltip-space: .5rem;
  --tooltip-transform: translateX(-50%);
  --tooltip-z-index: 50;
}

.c-tooltip {
  cursor: help;
  display: inline-block;
  position: relative;
  transform: translate3d(0, 0, 0);
  z-index: var(--tooltip-z-index);

  &::before,
  &::after {
    @extend %fade-out;
    position: absolute;
    transition: var(--global-duration) ease;
    z-index: var(--tooltip-z-index);

    /* Position Top */
    bottom: 100%;
    left: 100%;
    transform: translateX(-100%);

    @media (--md-up) {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &:hover,
  &:focus {
    &:before,
    &:after {
      @extend %fade-in;
    }

    /* Top */
    &::before { margin-bottom: calc(var(--tooltip-arrow-size) * 1 + var(--tooltip-space)); }

    &::after { margin-bottom: calc(var(--tooltip-arrow-size) * -1 + var(--tooltip-space)); }
  }

  &::before {
    background: var(--tooltip-background);
    border-radius: 2px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, .3);
    color: contrast(var(--tooltip-background));
    content: attr(data-tooltip);
    font-size: .83333rem;
    line-height: 1.5625;
    margin: 0;
    padding: var(--tooltip-space);
    min-width: 300px;
  }

  /* Arrow */
  &::after {
    background: transparent;
    border: var(--tooltip-arrow-size) solid transparent;
    content: '';
    position: absolute;
    z-index: calc(var(--tooltip-z-index) + 1);

    /* Top */
    border-top-color: var(--tooltip-background);
    margin-bottom: calc(var(--tooltip-arrow-size) * -2);
  }
}
