/* stylelint-disable */

.t-docs {

  .c-bar,
  .c-bar--top {
    @extend %row;
    background-color: var(--secondary-color);
    opacity: .9;

    nav:first-child {
      @extend %column-4;
    }

    nav:last-child {
      @extend %column-8;
    }
  }

  .o-brand {
    background-image: url('dist/img/navis-ui-logo.svg');
  }

  .section {
    border-bottom: 1px solid var(--grey-300);
    padding-bottom: 2em;
  }

  ::-moz-selection { background: var(--gold-300); }
  ::selection { background: var(--gold-300); }

  .c-menu {
    @mixin menu var(--secondary-color), var(--orange-500);

    > ul > li:hover > a + ul {
      position: absolute;
      text-align: left;
    }
  }


  .example {
    border: var(--grey-500) 1px solid;
    padding: 1em;
    position: relative;

    > * {
      position: relative !important;
    }
  }

  /*
  code,
  .hljs *:not(.hljs-rules) {
    color: var(--red-600) !important;
  }

  pre code {
    display: block;
    overflow: auto;
    padding: 1em;
  }

  code {
    background: #fffaf0;
    font-size: 14px;
    @media (--sm) {
      white-space: nowrap;
    }
  }
  */

  pre, code {
    background-color: rgba(0,0,0,.04);
  }

  pre code {
    background-color: transparent;
  }

  .token.punctuation {
    color: var(--grey-800);
  }

  .token.tag {
    color: var(--green-700);
  }

  .token.attr-name {
    color: var(--purple-a100);
  }

  .token.attr-value {
    color: var(--blue-700);
  }

  .token.function {
    color: var(--blue-600);
  }

  .token.string {
    color: var(--blue-700);
  }

  .token.comment {
    color: var(--grey-700);
  }

  .box-row {
    margin-bottom: 1rem;
  }

  .box,
  .box-first,
  .box--large,
  .box-nested,
  .box-row {
    background-color: var(--blue-600);
    color: var(--white);
    min-height: 2rem;
    overflow: hidden;
    padding: .75rem;
    position: relative;
    text-align: center;
    font-size: .8rem;
    i {
      position: relative;
      top: 3px;
    }
  }

  .box-first {
    background-color: var(--blue-700);
  }

  .box-nested {
    background-color: var(--blue-800);
  }

  .box--large {
    height: 5rem;
  }

  .icon-example {
    li {
      display: inline-block;
      list-style: none;
      overflow: hidden;
      margin: 0 35px 10px 0;
      padding: 0;
      width: 25%;
    }
    i {
      font-size: 26px;
      float: left;
    }
    code {
      width: 250px;
      display: block;
      float: right;
    }
  }
}

/* stylelint-enable */

