/* stylelint-disable rule-properties-order */

:root {
  --avatar-small: 40px;
  --avatar-medium: 75px;
  --avatar-large: 150px;
  --avatar-xlarge: 250px;
}
/* stylelint-enable */

@define-mixin avatar $size {
  border-radius: 5px;
  display: inline-block;
  height: $size;
  margin: 5px;
  width: $size;
}

@define-mixin icon $size, $color {
  color: $color !important;
  font-size: $size !important;
}

.t-default {
  img { width: 100%; }
}

.o-avatar {
  &--default {
    background-color: var(--grey-300);
    background-size: 30px;
    background-repeat: no-repeat;
    background-position: 50%;
    /* background-image: svg("md/svg-sprite-social#ic_person_24px", "[fill]: var(--white)"); */

    [src=""] { display: none; }
  }

  &--small {
    @mixin avatar var(--avatar-small);

    @media (--sm-up) { &\@sm { @mixin avatar var(--avatar-small); } }
    @media (--md-up) { &\@md { @mixin avatar var(--avatar-small); } }
    @media (--lg-up) { &\@lg { @mixin avatar var(--avatar-small); } }
  }

  &--medium {
    @mixin avatar var(--avatar-medium);

    @media (--sm-up) { &\@sm { @mixin avatar var(--avatar-medium); } }
    @media (--md-up) { &\@md { @mixin avatar var(--avatar-medium); } }
    @media (--lg-up) { &\@lg { @mixin avatar var(--avatar-medium); } }
  }

  &--large {
    @mixin avatar var(--avatar-large);

    @media (--sm-up) { &\@sm { @mixin avatar var(--avatar-large); } }
    @media (--md-up) { &\@md { @mixin avatar var(--avatar-large); } }
    @media (--lg-up) { &\@lg { @mixin avatar var(--avatar-large); } }
  }

  &--xlarge {
    @mixin avatar var(--avatar-xlarge);

    @media (--sm-up) { &\@sm { @mixin avatar var(--avatar-xlarge); } }
    @media (--md-up) { &\@md { @mixin avatar var(--avatar-xlarge); } }
    @media (--lg-up) { &\@lg { @mixin avatar var(--avatar-xlarge); } }
  }

}

/** @define o-icon */

.o-icon {
  direction: ltr;
  display: inline-block;
  font-family: 'Material Icons';
  font-feature-settings: 'liga';
  font-weight: normal;
  font-smoothing: antialiased;
  font-size: 24px;
  font-style: normal;
  height: 1em;
  letter-spacing: normal;
  line-height: 1;
  text-rendering: optimizeLegibility;
  text-transform: none;
  white-space: nowrap;
  width: 1em;
  word-wrap: normal;

  &--small,
  &--medium,
  &--large,
  &--xlarge {
    color: var(--blue-600) !important;
  }

  &--small { font-size: 18px !important; }

  &--regular { font-size: 24px !important; }

  &--medium { font-size: 36px !important; }

  &--large { font-size: 40px !important; }

  &--xlarge {
    font-size: 65px !important;
    margin: rs(.5) 0 0;

    @media (--md-up) {
      font-size: 90px !important;
    }
  }

  &--account {
    @mixin icon 50px, var(--grey-500);
  }

  &--success {
    color: var(--green-700);
    position: relative;

    &::before {
      content: '';
      background-color: var(--white);
      border-radius: 50%;
      height: 18px;
      left: 3px;
      position: absolute;
      top: 3px;
      width: 18px;
      z-index: -1;
    }
  }

  &--alert {
    color: var(--red-600);
    position: relative;

    &::before {
      content: '';
      background-color: var(--white);
      border-radius: 50%;
      height: 18px;
      left: 3px;
      position: absolute;
      top: 3px;
      width: 18px;
      z-index: -1;
    }
  }

  &--disabled {
    color: var(--grey-500);
    position: relative;

    &::before {
      content: '';
      background-color: var(--grey-700);
      border-radius: 50%;
      height: 18px;
      left: 3px;
      position: absolute;
      top: 3px;
      width: 18px;
      z-index: -1;
    }
  }

  &-pill {
    background-color: var(--grey-500);
    border-radius: 50px;
    height: 22px;
    position: relative;
    width: calc(21px * 2);
    z-index: -1;

    & i:first-child {
      left: -1px;
      position: absolute;
      top: -1px;
    }

    & i:last-child {
      position: absolute;
      right: -1px;
      top: -1px;
    }
  }
}

