/*---
 section: Forms
 title: Dropdowns
 ---

 ```example:html
 <div class="c-dropdown">
   <div class="c-dropdown__toggle">Option 1</div>
   <ul class="c-dropdown__content is-active" id="test-dropdown" tabindex="-1" aria-hidden="true">
     <li><button>Option 1</button></li>
     <li><button>Option 2</button></li>
     <li><button>Option 3</button></li>
   </ul>
 </div>
 ```

 */

@import stylep-dropdown;

:root {
  --callout-bg-color: var(--white);
  --dropdown-width: 144px;
}

%base-dropdown-container {
  height: 35px;
  position: relative;
  z-index: 20;
}

%base-dropdown {
  background-color: var(--white);
  border-radius: 4px;
  border: 1px solid var(--grey-600);
  color: rgba(0, 0, 0, .75);
  cursor: pointer;
  display: inline-block;
  font-size: 18px !important;
  font-weight: normal !important;
  margin: 0;
  padding: 2px 5px;
  position: relative;
  text-align: left;
}

.t-default {
  select {
    @extend %base-dropdown;
    width: 100%;
  }
}

.c-dropdown {
  @extend %base-dropdown-container;

  &--icon { @extend %base-dropdown-container; }

  & > button > i { transition: opacity var(--global-duration) linear; }

  &__toggle {
    background-color: var(--white);
    border-radius: 4px;
    border: 1px solid var(--grey-600);
    color: rgba(0, 0, 0, .75);
    cursor: pointer;
    display: inline-block;
    font-size: 18px !important;
    font-weight: normal !important;
    margin-top: 0 !important;
    min-width: calc(var(--dropdown-width) - 44px);
    padding: 0 5px;
    position: relative;
    text-align: left;

    @media (--md-up) {
      font-size: 16px !important;
      min-width: var(--dropdown-width);
    }

    &::before {
      @mixin triangle 'bottom', var(--black), 5px;
      position: absolute;
      right: 13px;
      top: 13px;
    }
  }

  &__content {
    @extend %dropdown-inline;
    @mixin dropdown-solid var(--callout-bg-color), var(--grey-100), rgba(0, 0, 0, .75), 4px, none, var(--grey-600) 0 0 7px;
    /* @extend %callout-inline;
    @mixin callout-solid var(--callout-bg-color);
    @mixin callout-top-right; */
    padding: 3px;
    position: absolute;
    right: 0;
    top: 2.25em;

    &::before {
      right: 20px !important;
    }

    &::after {
      display: none;
    }

    button {
      padding: 10px;
      width: 100%;
    }

    i {
      color: #444;
      top: 3px !important;
    }

    ul:not(:last-child) {
      border-bottom: 1px solid var(--grey-500);
      padding-bottom: 3px;
      margin-bottom: 3px;
    }

    .o-headline {
      cursor: default;
      font-size: 14px;
      font-weight: bold;
      text-transform: uppercase;
      &:hover,
      &:focus {
        background-color: transparent;
      }
    }

    .o-external {
      color: var(--grey-500);
    }
  }
}

.c-dropdown--icon {
  &__content {
    position: absolute;
    right: var(--gutter-width);
  }
}

