/*---
 section: Containers
 title: Modal
 ---

 Use simple utilities to hide or show elements across any span of devices

 * `.u-hidden`
 * `.u-show`

 */

.u-hidden,
%hidden { display: none !important; }

.u-show,
%show { display: inherit !important; }

@media print {
  .u-hidden\@print { @extend %hidden; }
}

@media (--sm) {
  .u-hidden\@sm { @extend %hidden; }
  .u-show\@sm { @extend %show; }
  .u-show\@md { @extend %hidden; }
  .u-show\@md-up { @extend %hidden; }
}

@media (--sm-up) { .u-hidden\@sm-up { @extend %hidden; } }

@media (--md) {
  .u-hidden\@md { @extend %hidden; }
  .u-show\@md { @extend %show; }
}

@media (--md-up) {
  .u-hidden\@md-up { @extend %hidden; }
  .u-show\@md-up { @extend %show; }
  .u-show\@sm { @extend %hidden; }
}

@media (--md-down) {
  .u-show\@lg { @extend %hidden; }
}

@media (--lg) {
  .u-hidden\@lg { @extend %hidden; }
}

@media (--lg-up) {
  .u-hidden\@lg-up { @extend %hidden; }
  .u-show\@lg { @extend %show; }
  .u-show\@md { @extend %hidden; }
}

%fade-out {
  opacity: 0;
  transition: visibility 0s 2s, opacity var(--global-duration) linear;
  visibility: hidden;
}

%fade-in {
  opacity: 1;
  transition: opacity var(--global-duration) linear;
  visibility: visible;
}
