/* Button */

%button-inline {
  appearance: none;
  background-color: initial;
  border-width: 0;
  cursor: pointer;
  display: inline-block;
  padding: 13px 18px;
  position: relative;
  line-height: 1;
  margin: 0 3% 3% 0;
  max-width: 100%;
  min-width: 44px;
  outline: 0;
  text-align: center;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  z-index: 10;
}

%button-block {
  @extend %button-inline;
  display: block;
  width: 100%;
}

@define-mixin button-solid
$color-passive: #333, 
$color-hover: #555, 
$color-text: #fff,
$radius-size: 4px,
$shadow: none {

  background-color: $color-passive;
  border-radius: $radius-size;
  color: $color-text;
  position: relative;
  transition: background-color .3s ease-in-out;
  @mixin-content;

  &::after {
    border-radius: $radius-size;
    box-shadow: $shadow;
    bottom: 0;
    content: '';
    left: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: opacity .3s ease-in-out;
    z-index: 5;
  }

  &:hover::after { opacity: 1; }

  &:hover,
  &:focus { background-color: $color-hover; }

  &[disabled] {
    opacity: .35;
    pointer-events: none;
  }

}

@define-mixin button-hollow
$color-passive: #555, 
$color-hover: #fff, 
$color-background: transparent,
$radius-size: 4px,
$border-width: 1px,
$shadow: none {

  background-color: $color-background;
  border-color: $color-passive;
  border-radius: $radius-size;
  border-style: solid;
  border-width: 1px;
  color: $color-passive;
  transition: background .3s ease-in-out, color .3s ease-in-out;
  @mixin-content;

  &:hover,
  &:focus {
    background-color: $color-passive;
    color: $color-hover;
  }

  &::after {
    border-radius: $radius-size;
    bottom: 0;
    box-shadow: $shadow;
    content: '';
    left: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: opacity .3s ease-in-out;
  }

  &:hover::after { opacity: 1; }

  &:hover,
  &:focus { 
    background-color: $color-passive;
    color: $color-hover;
  }

  &[disabled] {
    opacity: .35;
    pointer-events: none;
  }
}

@define-mixin button-circle-solid
$color-passive: #333, 
$color-hover: #555, 
$color-text: #fff,
$padding: 15px,
$shadow: none {
  @mixin button-solid $color-passive, $color-hover, $color-text, 50%, $shadow {
    padding: $padding;
  }
}

@define-mixin button-circle-hollow
$color-passive: #555, 
$color-hover: #fff, 
$color-background: transparent,
$padding: 15px,
$border-width: 1px,
$shadow: none {
  @mixin button-hollow $color-passive, $color-hover, $color-background, 50%, $border-width, $shadow {
    padding: $padding;
  }
}

/* Button Group */

%button-group-inline {
  box-sizing: border-box;
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 3%;

  & button {
    margin-right: 1px;
    margin-bottom: 1px;
  }
}

%button-group-block {
  @extend %button-group-inline;

  & button {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
  }
}

@define-mixin button-group-pill {
  & button {
    &:first-child {
      border-bottom-right-radius: 0 !important;
      border-top-right-radius: 0 !important;
    }

    &:not(:first-child):not(:last-child) {
      border-radius: 0 !important;
    }

    &:last-child {
      border-bottom-left-radius: 0 !important;
      border-top-left-radius: 0 !important;
    }
  }
}

